import React, { useEffect, useState } from 'react';
import Modal from 'react-modal';
import { numberOfWords } from '../word/WordsService';
import close from './close.svg';

const HelpComponent = () => {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(!open);
  const [numberWords, setNumberWords] = useState(0);
  useEffect(() => {
    numberOfWords().then((n) => setNumberWords(n));
    Modal.setAppElement('#root');
  }, []);

  return (
    <div>
      <button onClick={() => handleOpen(!open)}>Pomoc</button>
      <Modal
        isOpen={open}
        style={{
          content: {
            width: '80em',
            margin: 'auto'
          }
        }}
      >
        <div className="flex justify-between shadow-inner ">
          <h2 className="inline-block self-center text-3xl text-center w-full">Instrukcja</h2>
          <button
            type="button"
            className="close-button text-xl align-center cursor-pointer alert-del"
            onClick={handleOpen}
          >
            <span className="sr-only">Zamknij</span>
            <img src={close} alt="close" />
          </button>
        </div>
        <div>
          <p>Fiszki japoskie dla grupy z Profi-Lingua.</p>
          <p>Poziom A1.</p>
          <p>Rok 2024/2025</p>
          <h3>Liczba sw</h3>
          <p>Obecnie mamy {numberWords} nie unikalnych sw.</p>
          <h3>Wpisywanie odpowiedzi</h3>
          <p>
            eby uzyska znaki hiragan pisz mae litery, katakan wielkie. Sowa maj tylko jedn
            wersj, wic jak uyjesz zego alfabetu, to bdzie to bd.
          </p>
          <p>
            Jeeli korzystasz z telefonu komrkowego, to pamitaj, e domylnie pierwsza litera w
            tekcie jest wielka i pierwszy znak bdzie rozwizywany do katakany.
          </p>
          <p>eby wpisa gosk , to po wpisaniu n nacinij spacj.</p>
          <p>eby wpisa gosk  (dzu), musisz napisa du a nie dzu.</p>
          <p>eby wpisa gosk  (dzi), moesz napisa ji lub zi.</p>
          <p>
            Lista z wynikami zawiera dobre i ze odpowiedzi. W przypadku zych podana jest twoja
            odpowied.
          </p>
          <p>
            Jeeli jakie sowo ma kilka wersji w jzyku japoskim np. dugopis  i ,
            to moesz poda jedn.
          </p>
          <p>
            Spacje w rodku s usuwane np.  (denwabangoku) i   (denwa
            bangoku) s poprawnymi formami. Tak, wiem nie do koca jest to gramatyczne, ale tak jest
            atwiej to ogarn.
          </p>
        </div>
      </Modal>
    </div>
  );
};

export default HelpComponent;
